<template>
    <el-container v-loading="loading">
        <div class="rise x add">
            <div class="title">{{header}}</div>
            <div class="content">
                <div class="item" v-for="(item, index) in data" :key="index">
                    <span>{{item.name}}</span>
                    <div v-if="!item.type">
                        <el-input v-if="item.module === 'title'" v-model="title" :placeholder="item.placeholder"/>
                    </div>
                    <div v-if="item.type === 'select'">
                        <el-select v-if="item.module === 'type'" v-model="type" placeholder="全部">
                            <el-option
                                v-for="item in item.options"
                                :key="item.id"
                                :label="item.dValue"
                                :value="item.id"
                            ></el-option>
                        </el-select>
                    </div>
                </div>
                <mediaupload label="音视频上传：" ref="mediaup" :defaultValue="mediaDefault" style="marginBottom: 15px"/>
                <picupload label="背景图上传：" ref="picup" :defaultValue="picDefault"/>
                <!-- 媒体介绍 -->
                <div class="item">
                    <span>媒体介绍：</span>
                    <el-input type="textarea" :rows="4" placeholder="请输入媒体介绍" v-model="remark" />
                </div>
            </div>
            <div class="btn-list">
                <el-button @click="cancle()" class="btn">取消</el-button>
                <el-button @click="save()" class="btn">保存</el-button>
            </div>
        </div>
    </el-container>
</template>

<script>
import queryString from "querystring";
import { mediaState } from "data/listBody";
import Picupload from "components/picupload";
import Mediaupload from "components/mediaupload";
const headers = ["新建", "编辑"];
const defaultV = [
  { name: "媒体标题：", placeholder: "请输入媒体标题", module: "title" },
  {
    name: "媒体类型：",
    placeholder: "请选择媒体类型",
    module: "type",
    type: "select",
    options: mediaState
  }
];

export default {
  name: "add",
  data() {
    return {
      title: "",
      header: "",
      id: "",
      name: "",
      remark: "",
      type: "",
      types: "",
      mediaDefault: null, //默认地址
      picDefault: null, //默认地址
      data: defaultV,
      loading: false
    };
  },
  components: {
    Picupload,
    Mediaupload
  },
  mounted() {
    this.dataInit();
  },
  methods: {
    dataInit() {
      const { type, id } = this.$route.query;
      this.types = type;
      this.id = id;
      if (type !== "edit") return (this.header = headers[0]);
      this.header = headers[1];
      this.detailAjax();
    },
    editInit(res) {
      const { remark, title, pics, url, type } = res.data;
      this.remark = remark;
      this.title = title;
      this.type = type;
      this.picDefault = pics;
      this.mediaDefault = url;
    },
    detailAjax() {
      //查看详情
      this.loading = true;
      this.api.get(`/media/${this.id}`).then(res => {
          this.loading = false;
          this.editInit(res);
        })
        .catch(err => {
          console.log(err);
          this.loading = false;
          this.$message(err.message);
        });
    },
    editAjax() {
      const params = {
        remark: this.remark,
        title: this.title,
        type: this.type,
      };
      params.pics = this.$refs.picup.getData();
      params.url = this.$refs.mediaup.getData();
      const axiosType = ["post", "put"];
      let type = axiosType[0];
      if (this.types === "edit") {
        params.id = this.id;
        type = axiosType[1];
      }
      this.loading = true;
      this.api[type](`/media`, params, "form").then(res => {
          this.loading = false;
          this.$router.go(-1);
        })
        .catch(err => {
          this.loading = false;
          console.log(err);
          this.$message(err.message);
        });
    },
    save() {
      this.editAjax();
    },
    cancle() {
      //取消
      this.$router.go(-1);
    }
  }
};
</script>
 
<style lang="less" scoped>
</style>
 